import React from 'react';
import { Link } from 'react-router-dom';

const NotFoundPage = () =>  {
    return (
        <>
            <div className="c-drawer-navigation">
                <h1>404: Page Not Found</h1>
                <Link className="btn btn-primary" to="/">Go Home</Link>
            </div>    
        </>
    );
};
export default NotFoundPage;