import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import './NavigationBar.css';
import AppsRoundedIcon from '@material-ui/icons/AppsRounded';
import {CardDeck, Card, Modal} from 'react-bootstrap'
import { useLocation } from 'react-router-dom';

const NavigationBar = ({company, navCls, navRole, tabs}) => {

  const [drawerOpen, setDrawerOpen] = useState(false);
  const location = useLocation();
  let tabsToDisplay = tabs.filter(item => item.to !== location.pathname);

  function handleClick (event) {
      setDrawerOpen(false)        
      event.currentTarget.querySelector('.c-nav-item').click();
  }

  return (
    <>
      {company}
      <div className={navCls}>
        <AppsRoundedIcon style={{color: 'ivory'}} onClick={() => setDrawerOpen(true)}></AppsRoundedIcon>
      </div>
      {drawerOpen ? 
        <div> 
            <Modal centered show={drawerOpen} onHide={() => setDrawerOpen(false)} dialogClassName="c-drawer-modal-size">
                <Modal.Body>
                    <CardDeck>
                        {tabsToDisplay.map((tab, key) => (
                            <Card key={key} onClick={handleClick} onKeyDown={handleClick}> 
                                <Card.Img variant="top" src={require(/* webpackMode: "eager" */ `../icons/${tab.image}`)} />
                                <Card.Body>
                                    <Card.Title>{tab.title}</Card.Title>
                                    <Card.Text>
                                        {tab.description}
                                    </Card.Text>
                                </Card.Body>
                                <Card.Footer>
                                    <div className="c-drawer-navlink-parent">
                                        <NavLink className={tab.cls} to={tab.to} activeStyle={{fontWeight: '900'}}
                                            onClick={() => setDrawerOpen(false)} onKeyDown={() => setDrawerOpen(false)} exact>
                                            {tab.name}
                                        </NavLink>
                                    </div>
                                </Card.Footer>
                            </Card>              
                            ))
                        }
                    </CardDeck>
                </Modal.Body>
            </Modal>
        </div>
        : ''}
    </>
  );
};

export default NavigationBar;